namespace $ {
	export class $hyoo_sketch_element_markdown extends $hyoo_sketch_element_base {
		
		/**
		 * ```tree
		 * Element $mol_text text <= text?
		 * ```
		 */
		@ $mol_mem
		Element() {
			const obj = new this.$.$mol_text()
			
			obj.text = () => this.text()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Options *
		 * 	^
		 * 	markdown <= markdown_options
		 * ```
		 */
		Options() {
			return {
				...super.Options(),
				markdown: this.markdown_options()
			}
		}
		
		/**
		 * ```tree
		 * text_default \
		 * 	\# Hello world!
		 * 	\How are you?
		 * 	\**use markdwon here**
		 * ```
		 */
		text_default() {
			return "# Hello world!\nHow are you?\n**use markdwon here**"
		}
		
		/**
		 * ```tree
		 * text? <= text_default
		 * ```
		 */
		text(next?: any) {
			return this.text_default()
		}
		
		/**
		 * ```tree
		 * Text_option $mol_textarea
		 * 	style * height \150px
		 * 	value? <=> text?
		 * ```
		 */
		@ $mol_mem
		Text_option() {
			const obj = new this.$.$mol_textarea()
			
			obj.style = () => ({
				height: "150px"
			})
			obj.value = (next?: any) => this.text(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * markdown_options / <= Text_option
		 * ```
		 */
		markdown_options() {
			return [
				this.Text_option()
			] as readonly any[]
		}
	}
	
}

