namespace $ {
	export class $hyoo_sketch_element_header extends $hyoo_sketch_element_base {
		
		/**
		 * ```tree
		 * Head
		 * ```
		 */
		Head() {
			return this.Page().Head()
		}
		
		/**
		 * ```tree
		 * Page $mol_page
		 * 	title <= title
		 * 	Head => Head
		 * ```
		 */
		@ $mol_mem
		Page() {
			const obj = new this.$.$mol_page()
			
			obj.title = () => this.title()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Element <= Head
		 * ```
		 */
		Element() {
			return this.Head()
		}
		
		/**
		 * ```tree
		 * height_default 64
		 * ```
		 */
		height_default() {
			return 64
		}
		
		/**
		 * ```tree
		 * width_default 340
		 * ```
		 */
		width_default() {
			return 340
		}
		
		/**
		 * ```tree
		 * Options *
		 * 	^
		 * 	header <= header_options
		 * ```
		 */
		Options() {
			return {
				...super.Options(),
				header: this.header_options()
			}
		}
		
		/**
		 * ```tree
		 * title_default \Page title
		 * ```
		 */
		title_default() {
			return "Page title"
		}
		
		/**
		 * ```tree
		 * title? <= title_default
		 * ```
		 */
		title(next?: any) {
			return this.title_default()
		}
		
		/**
		 * ```tree
		 * Title_control $mol_string value? <=> title?
		 * ```
		 */
		@ $mol_mem
		Title_control() {
			const obj = new this.$.$mol_string()
			
			obj.value = (next?: any) => this.title(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Title_option $hyoo_sketch_option
		 * 	name @ \Title
		 * 	Control <= Title_control
		 * ```
		 */
		@ $mol_mem
		Title_option() {
			const obj = new this.$.$hyoo_sketch_option()
			
			obj.name = () => this.$.$mol_locale.text( '$hyoo_sketch_element_header_Title_option_name' )
			obj.Control = () => this.Title_control()
			
			return obj
		}
		
		/**
		 * ```tree
		 * header_options / <= Title_option
		 * ```
		 */
		header_options() {
			return [
				this.Title_option()
			] as readonly any[]
		}
	}
	
}

