namespace $ {
	export class $hyoo_sketch_palette extends $mol_page {
		
		/**
		 * ```tree
		 * domain
		 * ```
		 */
		domain() {
			return this.page().domain()
		}
		
		/**
		 * ```tree
		 * page $hyoo_sketch_page domain => domain
		 * ```
		 */
		@ $mol_mem
		page() {
			const obj = new this.$.$hyoo_sketch_page()
			
			return obj
		}
		
		/**
		 * ```tree
		 * title @ \Palette
		 * ```
		 */
		title() {
			return this.$.$mol_locale.text( '$hyoo_sketch_palette_title' )
		}
		
		/**
		 * ```tree
		 * body / <= List
		 * ```
		 */
		body() {
			return [
				this.List()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * type_list /
		 * 	\attach
		 * 	\button
		 * 	\check
		 * 	\date
		 * 	\header
		 * 	\image
		 * 	\input
		 * 	\link
		 * 	\markdown
		 * 	\map
		 * 	\select
		 * 	\speck
		 * 	\switch
		 * 	\text
		 * 	\textarea
		 * ```
		 */
		type_list() {
			return [
				"attach",
				"button",
				"check",
				"date",
				"header",
				"image",
				"input",
				"link",
				"markdown",
				"map",
				"select",
				"speck",
				"switch",
				"text",
				"textarea"
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * row_title* \
		 * ```
		 */
		row_title(id: any) {
			return ""
		}
		
		/**
		 * ```tree
		 * element_add*? null
		 * ```
		 */
		@ $mol_mem_key
		element_add(id: any, next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Row*0 $mol_button_minor
		 * 	title <= row_title*
		 * 	click? <=> element_add*?
		 * ```
		 */
		@ $mol_mem_key
		Row(id: any) {
			const obj = new this.$.$mol_button_minor()
			
			obj.title = () => this.row_title(id)
			obj.click = (next?: any) => this.element_add(id, next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * rows / <= Row*0
		 * ```
		 */
		rows() {
			return [
				this.Row("0")
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * List $mol_list rows <= rows
		 * ```
		 */
		@ $mol_mem
		List() {
			const obj = new this.$.$mol_list()
			
			obj.rows = () => this.rows()
			
			return obj
		}
	}
	
}

