namespace $ {
	export class $hyoo_sketch_element_icon extends $hyoo_sketch_element_base {
		
		/**
		 * ```tree
		 * Element $mol_view sub / <= Icon
		 * ```
		 */
		@ $mol_mem
		Element() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.Icon()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Not_found_icon $mol_icon_help_rhombus_outline
		 * ```
		 */
		@ $mol_mem
		Not_found_icon() {
			const obj = new this.$.$mol_icon_help_rhombus_outline()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Options *
		 * 	^
		 * 	icon <= icon_options
		 * ```
		 */
		Options() {
			return {
				...super.Options(),
				icon: this.icon_options()
			}
		}
		
		/**
		 * ```tree
		 * icon_path \
		 * ```
		 */
		icon_path() {
			return ""
		}
		
		/**
		 * ```tree
		 * Icon $mol_icon path <= icon_path
		 * ```
		 */
		@ $mol_mem
		Icon() {
			const obj = new this.$.$mol_icon()
			
			obj.path = () => this.icon_path()
			
			return obj
		}
		
		/**
		 * ```tree
		 * icon_name? \
		 * ```
		 */
		@ $mol_mem
		icon_name(next?: any) {
			if ( next !== undefined ) return next as never
			return ""
		}
		
		/**
		 * ```tree
		 * Icon_name_control $mol_string
		 * 	hint \$mol_icon_plus
		 * 	value? <=> icon_name?
		 * ```
		 */
		@ $mol_mem
		Icon_name_control() {
			const obj = new this.$.$mol_string()
			
			obj.hint = () => "$mol_icon_plus"
			obj.value = (next?: any) => this.icon_name(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Icon_name_option $hyoo_sketch_option
		 * 	name @ \Name
		 * 	Control <= Icon_name_control
		 * ```
		 */
		@ $mol_mem
		Icon_name_option() {
			const obj = new this.$.$hyoo_sketch_option()
			
			obj.name = () => this.$.$mol_locale.text( '$hyoo_sketch_element_icon_Icon_name_option_name' )
			obj.Control = () => this.Icon_name_control()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Icon_search_icon $mol_icon_magnify
		 * ```
		 */
		@ $mol_mem
		Icon_search_icon() {
			const obj = new this.$.$mol_icon_magnify()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Icon_search $mol_link
		 * 	uri \https://nin-jin.github.io/mol_icon
		 * 	target \_blank
		 * 	sub / <= Icon_search_icon
		 * ```
		 */
		@ $mol_mem
		Icon_search() {
			const obj = new this.$.$mol_link()
			
			obj.uri = () => "https://nin-jin.github.io/mol_icon"
			obj.target = () => "_blank"
			obj.sub = () => [
				this.Icon_search_icon()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Icon_search_option $hyoo_sketch_option
		 * 	name @ \Search icon here
		 * 	Control <= Icon_search
		 * ```
		 */
		@ $mol_mem
		Icon_search_option() {
			const obj = new this.$.$hyoo_sketch_option()
			
			obj.name = () => this.$.$mol_locale.text( '$hyoo_sketch_element_icon_Icon_search_option_name' )
			obj.Control = () => this.Icon_search()
			
			return obj
		}
		
		/**
		 * ```tree
		 * icon_options /
		 * 	<= Icon_name_option
		 * 	<= Icon_search_option
		 * ```
		 */
		icon_options() {
			return [
				this.Icon_name_option(),
				this.Icon_search_option()
			] as readonly any[]
		}
	}
	
}

