namespace $ {
	export class $hyoo_sketch_person_avatar extends $mol_view {
		
		/**
		 * ```tree
		 * id
		 * ```
		 */
		id() {
			return this.person().id()
		}
		
		/**
		 * ```tree
		 * name
		 * ```
		 */
		name() {
			return this.person().name()
		}
		
		/**
		 * ```tree
		 * online
		 * ```
		 */
		online() {
			return this.person().online()
		}
		
		/**
		 * ```tree
		 * person $hyoo_sketch_person
		 * 	id => id
		 * 	name => name
		 * 	online => online
		 * ```
		 */
		@ $mol_mem
		person() {
			const obj = new this.$.$hyoo_sketch_person()
			
			return obj
		}
		
		/**
		 * ```tree
		 * sub /
		 * 	<= Avatar
		 * 	<= Name
		 * 	<= Online
		 * ```
		 */
		sub() {
			return [
				this.Avatar(),
				this.Name(),
				this.Online()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Avatar $mol_avatar id <= id
		 * ```
		 */
		@ $mol_mem
		Avatar() {
			const obj = new this.$.$mol_avatar()
			
			obj.id = () => this.id()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Name $mol_paragraph title <= name
		 * ```
		 */
		@ $mol_mem
		Name() {
			const obj = new this.$.$mol_paragraph()
			
			obj.title = () => this.name()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Online $mol_speck value @ \online
		 * ```
		 */
		@ $mol_mem
		Online() {
			const obj = new this.$.$mol_speck()
			
			obj.value = () => this.$.$mol_locale.text( '$hyoo_sketch_person_avatar_Online_value' )
			
			return obj
		}
	}
	
}

