namespace $ {
	export class $hyoo_sketch_option extends $mol_row {
		
		/**
		 * ```tree
		 * sub /
		 * 	<= Name
		 * 	<= Control
		 * ```
		 */
		sub() {
			return [
				this.Name(),
				this.Control()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * name \
		 * ```
		 */
		name() {
			return ""
		}
		
		/**
		 * ```tree
		 * Name $mol_view sub / <= name
		 * ```
		 */
		@ $mol_mem
		Name() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.name()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Control $mol_view
		 * ```
		 */
		@ $mol_mem
		Control() {
			const obj = new this.$.$mol_view()
			
			return obj
		}
	}
	
}

