namespace $ {
	export class $hyoo_sketch_person_cursor extends $hyoo_sketch_person_avatar {
		
		/**
		 * ```tree
		 * sub /
		 * 	<= Cursor
		 * 	<= Avatar
		 * 	<= Name
		 * ```
		 */
		sub() {
			return [
				this.Cursor(),
				this.Avatar(),
				this.Name()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Cursor $mol_icon_cursor_default
		 * ```
		 */
		@ $mol_mem
		Cursor() {
			const obj = new this.$.$mol_icon_cursor_default()
			
			return obj
		}
	}
	
}

