namespace $ {
	export class $hyoo_sketch_project_list extends $mol_page {
		
		/**
		 * ```tree
		 * domain
		 * ```
		 */
		domain() {
			return this.person().domain()
		}
		
		/**
		 * ```tree
		 * person $hyoo_sketch_person domain => domain
		 * ```
		 */
		@ $mol_mem
		person() {
			const obj = new this.$.$hyoo_sketch_person()
			
			return obj
		}
		
		/**
		 * ```tree
		 * title @ \Projects
		 * ```
		 */
		title() {
			return this.$.$mol_locale.text( '$hyoo_sketch_project_list_title' )
		}
		
		/**
		 * ```tree
		 * project_name_default @ \New project
		 * ```
		 */
		project_name_default() {
			return this.$.$mol_locale.text( '$hyoo_sketch_project_list_project_name_default' )
		}
		
		/**
		 * ```tree
		 * tools /
		 * 	<= Person_link
		 * 	<= Project_add
		 * ```
		 */
		tools() {
			return [
				this.Person_link(),
				this.Project_add()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * body / <= List
		 * ```
		 */
		body() {
			return [
				this.List()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * foot /
		 * 	<= Online
		 * 	<= Source
		 * 	<= Lights
		 * ```
		 */
		foot() {
			return [
				this.Online(),
				this.Source(),
				this.Lights()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Project* $mol_link
		 * 	arg *
		 * 		project <= project_id*
		 * 		page null
		 * 		selected null
		 * 		preview null
		 * 	title <= project_name*
		 * ```
		 */
		@ $mol_mem_key
		Project(id: any) {
			const obj = new this.$.$mol_link()
			
			obj.arg = () => ({
				project: this.project_id(id),
				page: null as any,
				selected: null as any,
				preview: null as any
			})
			obj.title = () => this.project_name(id)
			
			return obj
		}
		
		/**
		 * ```tree
		 * person_id \0_0
		 * ```
		 */
		person_id() {
			return "0_0"
		}
		
		/**
		 * ```tree
		 * Person_icon $mol_icon_account
		 * ```
		 */
		@ $mol_mem
		Person_icon() {
			const obj = new this.$.$mol_icon_account()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Person_link $mol_link
		 * 	arg * person <= person_id
		 * 	hint @ \User
		 * 	sub / <= Person_icon
		 * ```
		 */
		@ $mol_mem
		Person_link() {
			const obj = new this.$.$mol_link()
			
			obj.arg = () => ({
				person: this.person_id()
			})
			obj.hint = () => this.$.$mol_locale.text( '$hyoo_sketch_project_list_Person_link_hint' )
			obj.sub = () => [
				this.Person_icon()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * project_add? null
		 * ```
		 */
		@ $mol_mem
		project_add(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Project_add_icon $mol_icon_plus
		 * ```
		 */
		@ $mol_mem
		Project_add_icon() {
			const obj = new this.$.$mol_icon_plus()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Project_add $mol_button_minor
		 * 	hint @ \Add project
		 * 	click? <=> project_add?
		 * 	sub / <= Project_add_icon
		 * ```
		 */
		@ $mol_mem
		Project_add() {
			const obj = new this.$.$mol_button_minor()
			
			obj.hint = () => this.$.$mol_locale.text( '$hyoo_sketch_project_list_Project_add_hint' )
			obj.click = (next?: any) => this.project_add(next)
			obj.sub = () => [
				this.Project_add_icon()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * projects /
		 * ```
		 */
		projects() {
			return [
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * List $mol_list rows <= projects
		 * ```
		 */
		@ $mol_mem
		List() {
			const obj = new this.$.$mol_list()
			
			obj.rows = () => this.projects()
			
			return obj
		}
		
		/**
		 * ```tree
		 * yard $hyoo_sync_client
		 * ```
		 */
		@ $mol_mem
		yard() {
			const obj = new this.$.$hyoo_sync_client()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Online $hyoo_sync_online yard <= yard
		 * ```
		 */
		@ $mol_mem
		Online() {
			const obj = new this.$.$hyoo_sync_online()
			
			obj.yard = () => this.yard()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Source $mol_link_source uri \https://github.com/hyoo-ru/sketch.hyoo.ru/
		 * ```
		 */
		@ $mol_mem
		Source() {
			const obj = new this.$.$mol_link_source()
			
			obj.uri = () => "https://github.com/hyoo-ru/sketch.hyoo.ru/"
			
			return obj
		}
		
		/**
		 * ```tree
		 * Lights $mol_lights_toggle
		 * ```
		 */
		@ $mol_mem
		Lights() {
			const obj = new this.$.$mol_lights_toggle()
			
			return obj
		}
		
		/**
		 * ```tree
		 * project_id* \
		 * ```
		 */
		project_id(id: any) {
			return ""
		}
		
		/**
		 * ```tree
		 * project_name* \
		 * ```
		 */
		project_name(id: any) {
			return ""
		}
	}
	
}

